<template>
  <div id="login-pane">
    <v-dialog persistent :value="dialog" width="500">
      <v-card>
        <v-toolbar flat>
          <v-toolbar-title>Anda harus login terlebih dahulu...</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-text-field
            label="Username.."
            v-model="item.username"
          />
          <v-text-field
            label="Password.."
            type="password"
            v-model="item.password"
          />
        </v-card-text>
        <v-card-text v-if="error" class="red">
          <div class="subtitle white--text">{{ errorMessage }}</div>
        </v-card-text>
        <v-card-actions>
          <v-btn
            v-if="!loading"
            @click="login"
            dark
            large
            depressed
            color="indigo"
          >Masuk</v-btn>
          <v-progress-circular
            v-else
            indeterminate
            color="grey lighten-2">
          </v-progress-circular>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { Auth__login } from "@/services/auth";

export default {
  name: 'Login',
  data: () => ({
    dialog: true,
    error: false,
    loading: false,
    errorMessage: '',
    item: {
      username: '',
      password: ''
    }
  }),
  methods: {
    async login() {
      this.loading = true;
      try {
        await Auth__login(this.item.username, this.item.password);
        localStorage.setItem("nue2014.username", this.item.username);
        this.$router.push("/app/data");
      } catch (err) {
        this.error = true;
        this.errorMessage = err.response.data;
        console.log(err.response);
      } finally {
        this.loading = false;
      }
    }
  }
}
</script>

<style>
#logout-icon {
  animation: spin 1s infinite linear;
}

@keyframes spin {
  from {transform:rotate(0deg);}
  to {transform:rotate(360deg);}
}

#login-pane {
  margin: 0;
  padding: 0;
  background: url('/img/bg3.jpg');
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  height: 100vh;
  width: 100vw;
}
</style>
